import React, {useState, useEffect, lazy} from "react";
import {
    Box,
    Typography,
    LinearProgress,
    Button,
    Skeleton,
    Chip,
} from "@mui/material";
import Grid from '@mui/material/Grid';
import { People  } from "@mui/icons-material";
import Alert from '@mui/material/Alert';
import Stack from '@mui/material/Stack';
import Dialog from '@mui/material/Dialog';
import DialogTitle from '@mui/material/DialogTitle';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogActions from '@mui/material/DialogActions';

import NetworkCheckIcon from '@mui/icons-material/NetworkCheck';
import CalendarMonthIcon from '@mui/icons-material/CalendarMonth';
import FlagIcon from '@mui/icons-material/Flag';


import CircularProgress from '@mui/material/CircularProgress';
import Backdrop from '@mui/material/Backdrop';

//import Flag from "react-world-flags";
const Flag = lazy(() => import('react-world-flags'))


const API_ENDPOINT = "api.mocaland.ir:88/QandShekan"






function ServiceList({user_id, active}) {
    const [loading, setLoading] = useState(true);
    const [Services , setServices] = useState([]);
    const [ServicesMSG , setServicesMSG] = useState("");
    const [PopUpText , setPopUpText] = useState({"status":false});
    const [PopUpLoading, setPopUpLoading] = React.useState(false);
    const ServiceItem = ({ service }) => {
        const { name, total, used, days, country, users, active, id } = service;
        const percent = (used / total) * 100;

        return (
            <Box
                sx={{
                    mt: 2,
                    mb: 2,
                    p: 2,
                    border: 1,
                    borderColor: "grey.300",
                    width: "95%",
                    bgcolor: active ? "#dcffdc" : "#ffdcdc",
                }}
            >
                <Typography variant="h6" sx={{direction:"rtl"}}>{name}</Typography>
                <Box display="flex" alignItems="center" sx={{ mt: 1 }}>
                    <Box width="100%" mr={1}>
                        <LinearProgress variant="determinate" value={percent} />
                    </Box>
                    <Box minWidth={35}>
                        <Typography variant="body2" color="text.secondary">{`${Math.round(
                            percent
                        )}%`}</Typography>
                    </Box>
                </Box>
                <Box display="flex" alignItems="center" sx={{ mt: 1 ,direction:"rtl"}}>
                    <Grid container spacing={1}>
                        <Grid item xs={7}>
                            <NetworkCheckIcon sx={{"height":"20px",mb:-0.5}}/>
                            {total-used < 0 ?
                                <Typography variant="p" color="text.secondary" sx={{"font-size":"14px"}} >{`حجم باقیمانده: 0`} گیگابایت</Typography>
                            :
                                <Typography variant="p" color="text.secondary" sx={{"font-size":"14px"}} >{`حجم باقیمانده: ${(total-used).toFixed(2)}`} گیگابایت</Typography>
                            }
                        </Grid>
                        <Grid item xs={5}>
                            <CalendarMonthIcon sx={{"height":"20px",mb:-0.5}}/>
                            <Typography variant="p" color="text.secondary" sx={{"font-size":"14px"}}>{`زمان باقیمانده: ${days}`}</Typography>
                        </Grid>
                    </Grid>
                </Box>

                <Box display="flex" alignItems="center" sx={{ mt: 1 ,direction:"rtl"}}>
                    <Grid container spacing={1}>
                        <Grid item xs={7}>
                            <FlagIcon sx={{"height":"20px",mb:-0.5}}/>
                            <Typography variant="p" color="text.secondary" sx={{ ml: 1 ,"font-size":"14px"}}>{`کشور: ${country}`}</Typography>
                            <Flag code={country} height="16" />
                        </Grid>
                        <Grid item xs={5}>
                            <People sx={{"height":"20px",mb:-0.5}}/>
                            <Typography variant="p" color="text.secondary" sx={{ ml: 1 ,"font-size":"14px"}}>{`کاربران مجاز: ${users}`}</Typography>
                        </Grid>
                    </Grid>
                </Box>


                {active ?
                    <>
                    <Box display="flex" justifyContent="space-between" sx={{ mt: 2 ,direction:"rtl"}}>
                        <Grid container spacing={1}>
                            <Grid item xs={6}>
                                <Button onClick={() => ResendConfig(id)}
                                        variant="contained" color="primary" sx={{width:"95%"}}>دریافت مجدد کانفیگ</Button>
                            </Grid>
                            <Grid item xs={6}>
                                <Button onClick={() => RenewConnectionIP(id)}
                                        variant="contained" color="secondary" sx={{width:"95%"}}>تغییر IP اتصال</Button>
                            </Grid>
                        </Grid>
                    </Box>
                    <Box display="flex" justifyContent="space-between" sx={{ mt: 2 ,direction:"rtl"}}>
                        <Grid container spacing={1}>
                            <Grid item xs={12}>
                                <Button onClick={() => ResendSubLink(id)}
                                        variant="contained" color="warning" sx={{width:"97.5%"}}>دریافت لینک آپدیت خودکار</Button>
                            </Grid>
                        </Grid>
                    </Box>
                    </>
                    :
                    <></>
                }
                <Box display="flex" alignItems="center" sx={{ mt: 2 }}>
                    <Chip
                        label={active ? "فعال" : "غیر فعال"}
                        color={active ? "success" : "error"}
                        sx={{ ml: "auto" }}
                    />
                </Box>

            </Box>
        );
    };

    useEffect(() => {
        setLoading(true);
        if (active) {
            var formdata = new FormData();
            formdata.append("action", "GetUserConfigs");
            formdata.append("user_id", user_id);
            var requestOptions = {
                method: 'POST',
                body: formdata,
                redirect: 'follow'
            };

            fetch(`https://${API_ENDPOINT}/api.php`, requestOptions)
                .then(response => response.text())
                .then(result => {
                    setLoading(false);
                    const res = JSON.parse(result);
                    if (res.status) {
                        setServices(res.services);
                    }else{
                        setServicesMSG(res.msg);
                    }
                })
                .catch(error => console.log('error', error));
        }
    }, [active]);


    function ResendConfig(id){
        setPopUpLoading(true);


        var formdata = new FormData();
        formdata.append("action", "ResendMyConfig");
        formdata.append("user_id", user_id);
        formdata.append("config_id", id);
        var requestOptions = {
            method: 'POST',
            body: formdata,
            redirect: 'follow'
        };

        fetch(`https://${API_ENDPOINT}/api.php`, requestOptions)
            .then(response => response.text())
            .then(result => {
                setPopUpLoading(false);
                const res = JSON.parse(result);
                if (res.status) {
                    setPopUpText({"status":true,"title":"موفق","text":res.msg});
                }else{
                    setPopUpText({"status":true,"title":"ناموفق","text":res.msg});
                }
            })
            .catch(error => {
                setPopUpLoading(false);
                setPopUpText({"status":true,"title":"ناموفق","text":"خطای ناشناخته ای رخ داد ، لطفا دقایقی بعد مجددا امتحان کنید!"});
            });

    }
    function ResendSubLink(id){
        setPopUpLoading(true);


        var formdata = new FormData();
        formdata.append("action", "ReSendSUB_Link");
        formdata.append("user_id", user_id);
        formdata.append("config_id", id);
        var requestOptions = {
            method: 'POST',
            body: formdata,
            redirect: 'follow'
        };

        fetch(`https://${API_ENDPOINT}/api.php`, requestOptions)
            .then(response => response.text())
            .then(result => {
                setPopUpLoading(false);
                const res = JSON.parse(result);
                if (res.status) {
                    setPopUpText({"status":true,"title":"موفق","text":res.msg});
                }else{
                    setPopUpText({"status":true,"title":"ناموفق","text":res.msg});
                }
            })
            .catch(error => {
                setPopUpLoading(false);
                setPopUpText({"status":true,"title":"ناموفق","text":"خطای ناشناخته ای رخ داد ، لطفا دقایقی بعد مجددا امتحان کنید!"});
            });
    }
    function RenewConnectionIP(id){
        setPopUpLoading(true);


        var formdata = new FormData();
        formdata.append("action", "ChangeConnectionIP");
        formdata.append("user_id", user_id);
        formdata.append("config_id", id);
        var requestOptions = {
            method: 'POST',
            body: formdata,
            redirect: 'follow'
        };

        fetch(`https://${API_ENDPOINT}/api.php`, requestOptions)
            .then(response => response.text())
            .then(result => {
                setPopUpLoading(false);
                const res = JSON.parse(result);
                if (res.status) {
                    setPopUpText({"status":true,"title":"موفق","text":res.msg});
                }else{
                    setPopUpText({"status":true,"title":"ناموفق","text":res.msg});
                }
            })
            .catch(error => {
                setPopUpLoading(false);
                setPopUpText({"status":true,"title":"ناموفق","text":"خطای ناشناخته ای رخ داد ، لطفا دقایقی بعد مجددا امتحان کنید!"});
            });
    }
    function ClosePopUpText(){
        setPopUpText({"status":false});
    }
    if (active)
        return (
            <Box sx={{ display: "flex", flexDirection: "column", alignItems: "center" ,fontFamily:"iranyekan"}}>
                {loading
                    ? // show skeleton while loading
                    Array.from(new Array(2)).map((_, index) => (
                        <Box
                            key={index}
                            sx={{
                                mt: 2,
                                mb: 2,
                                p: 2,
                                border: 1,
                                borderColor: "grey.300",
                                width: "95%",
                            }}
                        >
                            <Skeleton variant="text" width="40%" />
                            <Skeleton variant="rectangular" height={24} sx={{ mt: 1 }} />
                            <Skeleton variant="text" width="30%" sx={{ mt: 1 }} />
                            <Skeleton variant="rectangular" height={16} sx={{ mt: 1 }} />
                            <Skeleton variant="rectangular" height={24} sx={{ mt: 2 }} />
                            <Skeleton variant="rectangular" height={32} sx={{ mt: 2 }} />
                        </Box>
                    ))
                    : // show service items after loading
                    Services.map((service, index) => (
                        <ServiceItem key={index} service={service} />
                    ))}
                {Services.length === 0 && loading === false
                    ?
                    <Stack sx={{ width: '100%' ,direction:"rtl" }} spacing={2}>
                        <Alert severity="warning">{ServicesMSG}</Alert>
                    </Stack>
                    :
                    <></>
                }
                <Backdrop
                    sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }}
                    open={PopUpLoading}
                >
                    {/* CircularProgress برای نمایش یک انیمیشن بارگذاری استفاده می‌شود */}
                    <CircularProgress color="inherit" />
                </Backdrop>
                <div>
                    <Dialog open={PopUpText.status} sx={{direction:"rtl"}} onClose={()=>ClosePopUpText()}>
                        <DialogTitle>{PopUpText.title}</DialogTitle>
                        <DialogContent>
                            <DialogContentText>
                                {PopUpText.text}
                            </DialogContentText>
                        </DialogContent>
                        <DialogActions>
                            <Button onClick={()=>ClosePopUpText()}>بستن</Button>
                        </DialogActions>
                    </Dialog>
                </div>
            </Box>

        );
    return (<></>);
};

export default ServiceList;
